.collection-item {
  width: 22vw;
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center;
  position: relative;

  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }

  .custom-button {
    width: 80%;
    opacity: 0.7;
    position: absolute;
    top: 255px;
    display: none;
  }
    &:hover {
      .image {
        opacity: 0.8;
      }

      .custom-button {
        opacity: 0.85;
        display: flex;
      }

      .collection-footer {
        font-size: 1.3vw;
        font-weight: 600;
        color: whitesmoke;
        background-color: #206161;
        animation: tada 2s;
      }
}

  .collection-footer {
    width: 100%;
    height: 8%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    background-color: whitesmoke;

    .name {
      width: 70%;
      margin-bottom: 15px;
    }

    .price {
      width: 20%;
    }
  }
}